html,

.App {
  text-align: center;
}

.react-logo {
  height: 25vmin;
  pointer-events: none;
  color: #333;
}

@media (prefers-reduced-motion: no-preference) {
  .react-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

h1 {
  font-family: sans-serif;
  color: #333;
  font-size: 2rem;
}

p {
  font-family: sans-serif;
  size: 12px;
  color: #333;
}


.mainContent {
  background-color: #2d3748;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  box-shadow: 10px 10px 10px rgba(65, 65, 65, 0.8), 10px 10px 10px rgba(65, 65, 65, 0.8);
}

.App-link {
  /* color: #61dafb; */
  color: rgba(247,250,252,1);
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.logo-list {
  margin: 25px;
  color: white;
  justify-content: space-between;
}

li {
  list-style-type: none;
  justify-content: space-between;
}


.brand-icon {
  display: flex;
  float: left;
  justify-content: space-between;
  // color: #10171d;
  background-color: #e3edf7;
  height: 75px;
  width: 75px;
  margin: 0 15px;
  border-radius: 8px;
  text-align: center;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  box-shadow: 6px 6px 10px -1px rgba(0,0,0,0.15),
              -6px -6px 10px -1px rgba(255,255,255,0.7);
  color: #777;
  border: 1px solid rgba(0,0,0,0.01);
  transition: transform 0.5s;
}

.brand-icon:hover {
  color:orange;
  box-shadow: inset 4px 4px 6px -1px rgba(0,0,0,0.2),
              inset -4px -4px 6px -1px rgba(255,255,255,0.7),
            -0.5px -0.5px 0px rgba(255,255,255,1),
            0.5px 0.5px 0px rgba(0,0,0,0.15),
            0px 12px 10px -10px rgba(0,0,0,0.05);
  border: 1px solid rgba(0,0,0,0.01);
  transform: translateY(2px);
  transform: scale(0.9);
}

i {
  size: 32px;
  color: blue;
}

.mainBox {
  position: absolute;
  border-radius: 50px;
  padding: 5px;
  height: 35vw;
  width: 50vw;
  border: 3px solid lightcoral;
  // background-color: rgb(202, 202, 202);
  background-color: #e3edf7; 
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  overflow: auto;
  box-shadow: -10px 10px 10px rgba(65, 65, 65, 0.4), 10px 10px 10px rgba(65, 65, 65, 0.4);
}


@media (max-width: 300px) {
  .logo-list {
      width: 100%;
      float: none;
      border: none;
  }
}

@media (max-width: 1024px) {
  .mainBox {
      width: 70%;
      height: 75%;
      float: none;
  }
}


  body.light-mode{
    background-color: #fff;
    color: #333;
    transition: background-color 0.3s ease;
  }
  
  body.dark-mode {
    color: #dfdfdf;

    .mainBox {
      background-color: #282c34;
    }

    .navbar {
      background-color: #1b2938;
    }

    h1 {
      color: white;
    }

    p {
      color: white;
    }
    
    .react-logo {
      color: #fff;
    }

    .brand-icon {
      background-color:  #282c34;
      color: burlywood;
      box-shadow: 6px 6px 10px -1px rgba(0,0,0,0.15),
      -6px -6px 10px -1px rgba(36, 36, 36, 0.7);
border: 1px solid rgba(0,0,0,0.01);
transition: transform 0.5s;
    }
    .brand-icon:hover {
      color:orange;
      box-shadow: inset 4px 4px 6px -1px rgba(0,0,0,0.2),
      inset -4px -4px 6px -1px rgba(36, 36, 36, 0.7),
    -0.5px -0.5px 0px rgb(36, 36, 36),
    0.5px 0.5px 0px rgba(0,0,0,0.15),
    0px 12px 10px -10px rgba(0,0,0,0.05);
border: 1px solid rgba(0,0,0,0.01);
transform: translateY(2px);
transform: scale(0.9);
    }

    .dark-mode-toggle > button {
      color: #999;
      &:last-child {
        color: lightblue;
      }
    }
  }
  .navbar {
    position: fixed;
    top: 0;
    width: 100%;
    display: flex;
    background-color: #95d3ff;
    padding: 20px;
  }


button {
    color: #006688;
    background-color: transparent;
    border: none;
    font-size: 1em;
    padding: 0;
  }

body.dark-mode .content button {
  color: #ee09;
}

.dark-mode-toggle {
  display: flex;
  margin: 0 auto;
  & > button {
    font-size: 1.2em;
    background: none;
    border: none;
    color: #ffe600;
    cursor: pointer;
    transition: color 0.3s ease;
    &:last-child {
      color: #666;
    }

    &:focus {
      outline: none;
    }
  }
}

.toggle-control {
  position: relative;
  padding: 0 4px;
  display: flex;
  align-items: center;
}

input[type='checkbox'].dmcheck {
  width: 40px;
  height: 10px;
  background: #555;
  position: relative;
  border-radius: 5px;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  cursor: pointer;
  vertical-align: 2px;
  outline: none;

  &:checked + label {
    left: 30px;
  }

  &:focus-visible {
    outline: solid 2px white;
  }

  & + label {
    display: inline-block;
    width: 18px;
    height: 18px;
    border-radius: 50%;
    transition: all 0.3s ease;
    cursor: pointer;
    position: absolute;
    left: 2px;
    background: #fff;
    opacity: 0.9;
    background-color: #f6f6f6;
  }
}

a {
  padding: 5px;
  background-color: #e3edf7;
  height: 75px;
  width: 75px;
  margin: 0 15px;
  border-radius: 8px;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  box-shadow: 6px 6px 10px -1px rgba(0,0,0,0.15),
              -6px -6px 10px -1px rgba(255,255,255,0.7);
  color: #777;
}